.com-active-grey {
  position: relative;
}
.com-active-grey:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: inherit;
  border-color: #000;
  border-radius: inherit;
  background: #000;
  opacity: 0;
}
.com-active-grey:active:before {
  opacity: 0.05;
}
.com-active-white {
  position: relative;
}
.com-active-white:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: inherit;
  border-color: #fff;
  border-radius: inherit;
  background: #fff;
  opacity: 0;
}
.com-active-white:active:before {
  opacity: 0.1;
}
.com-site-card {
  box-sizing: border-box;
  width: 100%;
  color: #323233;
}
.com-site-card .header {
  display: flex;
}
.com-site-card .header .aqiInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 13.867vw;
  height: 13.867vw;
  text-shadow: 0 1px 0 rgba(0,0,0,0.15);
  color: #fff;
  border-radius: 100%;
  text-shadow: 0px 1px 0px rgba(0,0,0,0.15);
}
.com-site-card .header .aqiInfo .value {
  font-size: 4.8vw;
  font-weight: bold;
  line-height: 6.667vw;
}
.com-site-card .header .aqiInfo .quality {
  font-size: 2.667vw;
  line-height: 3.733vw;
}
.com-site-card .header .stationInfo {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  margin-left: 3.2vw;
}
.com-site-card .header .stationInfo .stationName {
  font-size: 4.267vw;
  font-weight: normal;
  line-height: 5.867vw;
}
.com-site-card .header .stationInfo .bottom {
  display: flex;
  align-items: center;
  width: 100%;
}
.com-site-card .header .stationInfo .bottom .discribe {
  font-size: 3.2vw;
  line-height: 5.867vw;
}
.com-site-card .header .stationInfo .bottom .updataTime {
  flex: 1;
  font-size: 2.667vw;
  text-align: right;
  line-height: 3.733vw;
  color: #999;
}
.com-site-card .header .stationInfo .bottom .arrow {
  font-size: 4.267vw;
  color: #999;
  margin-left: 1.067vw;
}
.factorBox {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 1px;
}
.factorBox .factor {
  display: flex;
  align-items: center;
  width: 22.4vw;
  font-size: 3.467vw;
  line-height: 4.8vw;
  margin: 4.267vw 0 0;
}
.factorBox .factor .circle {
  width: 2.667vw;
  height: 2.667vw;
  border: 0.533vw solid rgba(0,0,0,0);
  border-radius: 100%;
}
.factorBox .factor .value {
  width: 6.933vw;
  font-weight: normal;
  text-align: left;
  margin: 0 2.133vw;
  color: #333;
}
.factorBox .factor .value.primary {
  color: #ff4842;
}
.factorBox .factor .name {
  color: #666;
}
.com-head {
  height: 24.533vw;
  margin: 0 4.267vw;
  border-bottom: 0.5px solid #eaecee;
  color: #323233;
}
.com-head .title {
  text-align: left;
  font-weight: bold;
  font-size: 7.467vw;
  height: 9.067vw;
  line-height: 9.067vw;
  margin-bottom: 1.067vw;
}
.com-head .content {
  height: 5.867vw;
}
.com-head .content .time {
  font-weight: bold;
  font-size: 3.467vw;
}
.com-head .content .time .arrow {
  font-weight: bold;
  font-size: 4.267vw;
}
.com-head .content .address {
  font-weight: bold;
  font-size: 3.467vw;
}
.com-head .content .address .location,
.com-head .content .address .arrow {
  font-weight: bold;
  font-size: 4.267vw;
}
.com-head .content .address .location {
  margin-right: 1.333vw;
}
.com-source {
  margin: 0 0 13.333vw;
}
.com-source .source-item {
  text-align: center;
  font-size: 3.2vw;
  line-height: 4.533vw;
  margin-bottom: 1.333vw;
  color: #969799;
}
.com-home {
  position: fixed;
  bottom: 6.4vw;
  right: 6.4vw;
  width: 11.733vw;
  height: 11.733vw;
  background: #fff;
  box-shadow: 0px 0.7px 0.667vw rgba(0,0,0,0.02), 0px 0.427vw 4.267vw rgba(0,0,0,0.028), 0px 0.8vw 3.013vw rgba(0,0,0,0.035), 0px 1.44vw 5.36vw rgba(0,0,0,0.042), 0px 2.667vw 10.027vw rgba(0,0,0,0.05), 0px 6.4vw 24vw rgba(0,0,0,0.07);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.com-home i {
  display: inline-block;
  font-size: 5.867vw;
  color: #000;
}
.com-stackPage {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 999;
  overflow: auto;
}
.com-stackPage-enter-active,
.com-stackPage-leave-active {
  transition: 0.3s;
}
.com-stackPage-enter,
.com-stackPage-leave-to {
  left: 100%;
  opacity: 0;
}
